<template>
  <div class="dark1Eng">
    <span class="bg"></span>
    <v-card id="container" dark max-width="600" class="mx-auto">
      <v-toolbar extended class="mt-1" color="blue-grey" dark>
        <v-toolbar-title class="flex text-center">
          <h2 class="text-center mt-10">Quiz First Season</h2>
        </v-toolbar-title>
      </v-toolbar>

      <v-progress-linear color="lime lighten-1" :value="progress"></v-progress-linear>


    <v-list flat subheader two-line v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">

      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-layout align-end justify-end>
          <v-btn v-bind="attrs" v-on="on" icon text color="lime lighten-1" :disabled="!next">
            <v-icon>mdi-information</v-icon>
          </v-btn>
          </v-layout>
        </template>
        
        <v-card dark>
          <v-card-title class="headline">
            {{ element.question }}
          </v-card-title>

          <v-card-text class="my-5">
            {{ element.detail }}
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-layout align-center justify-center>
              <v-btn color="primary" text @click="dialog = false">
              Close
              </v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <h1 class="text-center">Question {{ b }}/{{ questions.length }}</h1>
      
      <v-list-item class="d-flex justify-center text-center" >{{ element.question }}</v-list-item>
      <v-divider class="mt-10"></v-divider>

      <v-list-item-group color="blue-grey--text">
        <template v-for="(item, index) in element.suggestions">
      <v-list-item id="list" class="d-flex justify-center my-2" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)" :disabled="lockSelection">
        {{ item.suggestion }}
        <v-icon class="mx-5" color="white" v-if="select ? item.correct : ''">mdi-check</v-icon>
        <v-icon class="mx-5" color="white" v-if="select ? !item.correct : ''">mdi-close</v-icon>
      </v-list-item>
      <v-divider v-if="index < element.suggestions.length - 1" :key="`${index}-divider`"></v-divider>
      </template>
      </v-list-item-group>
    </v-list>

      <v-divider></v-divider>

      <v-card dark max-width="600" class="mx-auto" v-if="score_show">
        <h2 class="text-center my-4">Your score </h2>
        <h2 class="text-center my-4">{{ score }}/{{ questions.length }}</h2>
        <v-card-actions class="justify-center">
          <v-btn class="mx-10" @click="restartQuiz">Restart <v-icon right>mdi-reload</v-icon></v-btn>
          <v-btn class="mx-10" @click="$router.push('/en')">Select <v-icon right>mdi-home</v-icon></v-btn>
        </v-card-actions>
          <v-divider class="mt-5"></v-divider>
      </v-card>

      <v-card-actions class="justify-center">
        <v-btn class="mx-10" color="blue-grey darken-1" @click="skipQuestion">
          Skip
        </v-btn>
        <v-btn :disabled="!next" class="mx-10" color="blue-grey darken-1" @click="nextQuestion">
          Next
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      questions: [
        {
          detail:"In the letter his father leaves to Jonas, it is explained that Michael is actually Mikkel Nielsen, who was brought into the past when he was little (Jonas realizes he is Martha's grandson).",
          question:'What does Michael Kahnwald leave to his son Jonas before he hangs himself?',
          suggestions: [
            {suggestion:'A book'},
            {suggestion:'A letter', correct: true},
            {suggestion:'A futuristic flashlight'},
            {suggestion:'A Geiger counter'},
          ]
        },
        {
          detail:"Erik Obendorf was the first boy to go missing in Winden in 2019 (to be followed by Mikkel and Yasin), which subsequently revealed the Dark and mysterious side of the town.",
          question:"Who has already disappeared in Winden, a few days before the start of the events narrated in the series?",
          suggestions: [
            {suggestion:'Yasin'},
            {suggestion:'Mikkel'},
            {suggestion:'Erik', correct: true},
            {suggestion:'Mads'},
          ]
        },
        {
          detail:"This mysterious character knows more than all the others; he tries to change things, but only after a few episodes will we discover his true identity: he is really an adult Jonas, who came from the future.",
          question:"Who arrives at Regina Tiedemann's hotel the day after Mikkel Nielsen's disappearance?",
          suggestions: [
            {suggestion:'The Stranger', correct: true},
            {suggestion:'Ulrich Nielsen'},
            {suggestion:'Noah'},
            {suggestion:'Helge Doppler'},
          ]
        },
        {
          detail:"Thanks to the discovery of this map, Jonas will decide to visit the caves of the city, in this way he will have the confirmation of what his father said and the existence of time travel.",
          question:'What does Jonas find in the attic where his father hanged himself?',
          suggestions: [
            {suggestion:'His will'},
            {suggestion:'A letter'},
            {suggestion:'A map of the caves', correct: true},
            {suggestion:'The book "Time travel"'},
          ]
        },
        {
          detail:"Claudia is one of the most enigmatic characters, we know that she works against Adam and that she helps Jonas, but we don't know what her purpose is. Furthermore, it is deduced that she traveled between worlds as an adult.",
          question:'What work does Claudia Tiedemann do in 1986?',
          suggestions: [
            {suggestion:'Headmaster'},
            {suggestion:'Journalist'},
            {suggestion:'Doctor'},
            {suggestion:'Director of the nuclear power plant', correct: true},
          ]
        },
        {
          detail:"Mads Nielsen, Ulrich's younger brother, is the very first child missing in Winden; his body will be found in 2019, the day after Mikkel's disappearance.",
          question:'In 1986 Winden was shaken by the disappearance of a child. Who is it about?',
          suggestions: [
            {suggestion:'Mikkel'},
            {suggestion:'Erik'},
            {suggestion:'Mads', correct: true},
            {suggestion:'Yasin'},
          ]
        },
        {
          detail:"Initially Mikkel lives in the hospital, is treated by the nurse Ines who becomes attached to him and so he later decides to adopt him (it is not yet known why he filled him with drugs).",
          question:'Who is taking care of Mikkel Nielsen when he arrives in 1986?',
          suggestions: [
            {suggestion:'Jana Nielsen'},
            {suggestion:'Egon Tiedemann'},
            {suggestion:'Katharina Nielsen'},
            {suggestion:'Ines Kahnwald', correct: true},
          ]
        },
        {
          detail:"Elisabeth tells her mother that she met a person named Noah, so from that moment Charlotte will start investigating him as a possible suspect in the disappearance of the children.",
          question:'Who meets Elisabeth Doppler on her way home from school?',
          suggestions: [
            {suggestion:'Yasin'},
            {suggestion:'Hegel Doppler'},
            {suggestion:'Noah', correct: true},
            {suggestion:'Peter Doppler'},
          ]
        },
        {
          detail:"During an unexpected encounter, Noah gives Elisabeth a watch engraved with Charlotte's name. We will not understand the reason for this gesture until it is revealed that Charlotte is Noah's daughter.",
          question:'What item does Noah give Elisabeth to Charlotte?',
          suggestions: [
            {suggestion:'A photo'},
            {suggestion:'A pendant'},
            {suggestion:'A gold bracelet'},
            {suggestion:'A pocket watch', correct: true},
          ]
        },
        {
          detail:"When Yasin, Elisabeth's friend, meets Helge, the latter introduces himself saying he was sent by Noah; Thus ends the episode, and in the next one we discover that Yasin has also disappeared.",
          question:'Who does Yasin meet on his way to school alone through the woods?',
          suggestions: [
            {suggestion:'Noah'},
            {suggestion:'Helge Doppler', correct: true},
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'Nessuno di questi'},
          ]
        },
        {
          detail:"In 1986, Noah visits Mikkel several times in the hospital, on these occasions the child turns out to be very cunning. From here we can think that in the following years it will act in some mysterious way.",
          question:'"God has a plan for every human being, even for you." Who says these words to Mikkel when he is in the hospital?',
          suggestions: [
            {suggestion:'The Stranger'},
            {suggestion:'Ines Kahnwald'},
            {suggestion:'Noah', correct: true},
            {suggestion:'Helge Doppler'},
          ]
        },
        {
          detail:"In this way, Jonas discovers about the letter left by Michael, which Ines had hidden from him, and begins to investigate discovering things that he could never have imagined.",
          question:"What's in the package Jonas Kahnwald receives from The Stranger?",
          suggestions: [
            {suggestion:'A letter, a Geiger counter and a book'},
            {suggestion:'A letter, a torch and a book'},
            {suggestion:'A flashlight, a map and a letter'},
            {suggestion:'A flashlight, a letter and a Geiger counter', correct: true},
          ]
        },
        {
          detail:"During the season we see Bartosz being manipulated by Noah, which brings him to the knowledge of many events that would occur later, in order to make him take certain actions.",
          question:'Who introduces Bartosz Tiedemann to the knowledge of time travel?',
          suggestions: [
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'The Stranger'},
            {suggestion:'Noah', correct: true},
            {suggestion:'Adam'},
          ]
        },
        {
          detail:"We know that Tronte has had multiple extramarital affairs (in fact, for this reason, he was not at home when his son Mads disappeared). It is theorized that Regina was born from their relationship.",
          question:'Who did Tronte Nielsen have an affair with?',
          suggestions: [
            {suggestion:'Ines Kahnwald'},
            {suggestion:'Doris Tiedemann'},
            {suggestion:'Claudia Tiedemann', correct: true},
            {suggestion:'None of these'},
          ]
        },
        {
          detail:"Thanks to this discovery, engineered by Claudia, Ulrich begins to investigate the mysterious disappearances near the caves, and he too finds himself traveling through time.",
          question:'Thanks to what detail does Ulrich Nielsen understand that the body found in the woods is that of his brother Mads?',
          suggestions: [
            {suggestion:'A sweatshirt'},
            {suggestion:'The shoes'},
            {suggestion:'A cut on the chin', correct: true},
            {suggestion:'A walkman'},
          ]
        },
        {
          detail:"When he discovers his father's true identity, Jonas decides to investigate and, arriving at the caves, crosses the passage towards 1986, where he will meet Mikkel who disappeared from 2019 a few days earlier.",
          question:'When does Jonas Kahnwald make his first time travel?',
          suggestions: [
            {suggestion:'1987'},
            {suggestion:'1986', correct: true},
            {suggestion:'1921'},
            {suggestion:'2019'},
          ]
        },
        {
          detail:"Ulrich, investigating the disappearances, notices suspicious behavior in the elderly Helge, this obsession will lead him to want to kill Helge (1990) even when he was still a child.",
          question:'According to Ulrich Nielsen, who is the main suspect in the disappearance of Mikkel and the other boys?',
          suggestions: [
            {suggestion:'Noah'},
            {suggestion:'Peter Doppler'},
            {suggestion:'The Stranger'},
            {suggestion:'Helge Doppler', correct: true},
          ]
        },
        {
          detail:"When Jonas attempts to take Mikkel with him, The Stranger stops him by telling him that if he did, his entire existence would be compromised.",
          question:'By whom is Jonas stopped trying to bring Mikkel back to his time?',
          suggestions: [
            {suggestion:'From Noah'},
            {suggestion:'From The Stranger', correct: true},
            {suggestion:'From Ines Kahnwald'},
            {suggestion:'From Egon Tiedemann'},
          ]
        },
        {
          detail:"Ulrich, convinced of Helge's guilt, decides to follow him. They then head to the caves, but Helge crossing the passage will go in 1986, while Ulrich, mistakenly, ends up in 1953.",
          question:'When does Ulrich Nielsen arrive traveling through the caves?',
          suggestions: [
            {suggestion:'1953', correct: true},
            {suggestion:'1987'},
            {suggestion:'1954'},
            {suggestion:'1986'},
          ]
        },
        {
          detail:"The Stranger goes to Tannhaus to have an explanation of what is written in his book; when the watchmaker asks why this interest, the stranger reveals that what he wrote is true.",
          question:'Who reveals to H.G. Tanhaus (the watchmaker) the existence of the passage in the caves and time travel?',
          suggestions: [
            {suggestion:'Ulrich Nielsen'},
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'The Stranger', correct: true},
            {suggestion:'Noah'},
          ]
        },
        {
          detail:"Aleksander arrives in Winden in 1986 as a fugitive. We see him hiding in the woods an envelope with a gun and a passport from which we discover his true identity: Boris Niewald (Nielsen + Kahnwald)",
          question:"What is Aleksander Tiedemann's real name?",
          suggestions: [
            {suggestion:'Aleksander Köhler'},
            {suggestion:'Boris Köhler'},
            {suggestion:'Aleksander Niewald'},
            {suggestion:'Boris Niewald', correct: true},
          ]
        },
        {
          detail:"In 1953, Tannhaus receives a visit from the elderly Claudia who gives him plans to build the portable time machine, which he will complete 33 years later seeing that of The Stranger.",
          question:'Who commissions and when to H.G. Tanhaus (the watchmaker) the construction of the portable time machine?',
          suggestions: [
            {suggestion:'Claudia Tidemann in 1953', correct: true},
            {suggestion:'Noah in 1953'},
            {suggestion:'Claudia Tidemann in 1954'},
            {suggestion:'The Stranger in 1986'},
          ]
        },
        {
          detail:"While Peter is in the bunker, a gash appears in midair from which the body of a child emerges who he discovers, from the school document, to be Mads Nielsen. Peter will call Tronte who will remain incredulous.",
          question:"Which boy's body appears in the bunker, in front of Peter Doppler, on the evening of November 4, 2019?",
          suggestions: [
            {suggestion:'Mads', correct: true},
            {suggestion:'Yasin'},
            {suggestion:'Erik'},
            {suggestion:'Mikkel'},
          ]
        },
        {
          detail:"When the elderly Helge arrives in 1986, he tries to convince his adult self not to follow Noah, but failing to do so, he decides to cause an accident in which he is killed.",
          question:'How did Helge Doppler die as an elder?',
          suggestions: [
            {suggestion:'Of old age'},
            {suggestion:'In a car accident', correct: true},
            {suggestion:'Because of an illness'},
            {suggestion:'Killed by Noah'},
          ]
        },
        {
          detail:"When The Stranger activates the briefcase in the caves, in the bunker, where Helge and Jonas are located, a wormhole is created; touching it, Helge (from 1953) ends in 1986 and Jonas (from 1986) goes to 2052.",
          question:'What epochs do Helge and Jonas end up in after touching each other through the wormhole?',
          suggestions: [
            {suggestion:'Helge 2019 - Jonas 1986'},
            {suggestion:'Helge 1953 - Jonas 2052'},
            {suggestion:'Helge 1986 - Jonas 2052', correct: true},
            {suggestion:'Helge 2052 - Jonas 2019'},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
      lockSelection: false,
      dialog: false,
    }
  },
  
  methods:{

    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
}
</script>