<template>
  <div class="dark3">
    <span class="bg3"></span>
    <v-card id="container" dark max-width="600" class="mx-auto">
      <v-toolbar extended class="mt-1" color="blue-grey" dark>
        <v-toolbar-title class="flex text-center">
          <h2 class="text-center mt-10">Quiz Terza Stagione</h2>
        </v-toolbar-title>
      </v-toolbar>

      <v-progress-linear color="lime lighten-1" :value="progress"></v-progress-linear>

      <v-list flat subheader two-line v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">

      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-layout align-end justify-end>
          <v-btn v-bind="attrs" v-on="on" icon text disabled>
            <v-icon>mdi-information</v-icon>
          </v-btn>
          </v-layout>
        </template>
        
        <v-card dark>
          <v-card-title class="headline">
            {{ element.question }}
          </v-card-title>

          <v-card-text class="my-5">
            {{ element.detail }}
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-layout align-center justify-center>
              <v-btn color="primary" text @click="dialog = false">
              Chiudi
              </v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <h1 class="text-center">Domanda {{ b }}/{{ questions.length }}</h1>
      
      <v-list-item class="d-flex justify-center text-center" >{{ element.question }}</v-list-item>
      <v-divider class="mt-10"></v-divider>

      <v-list-item-group color="blue-grey--text">
        <template v-for="(item, index) in element.suggestions">
      <v-list-item id="list" class="d-flex justify-center my-2" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)" :disabled="lockSelection">
        {{ item.suggestion }}
        <v-icon class="mx-5" color="white" v-if="select ? item.correct : ''">mdi-check</v-icon>
        <v-icon class="mx-5" color="white" v-if="select ? !item.correct : ''">mdi-close</v-icon>
      </v-list-item>
      <v-divider v-if="index < element.suggestions.length - 1" :key="`${index}-divider`"></v-divider>
      </template>
      </v-list-item-group>
    </v-list>

      <v-divider></v-divider>

      <v-card dark max-width="600" class="mx-auto" v-if="score_show">
        <h2 class="text-center my-4">Il tuo punteggio </h2>
        <h2 class="text-center my-4">{{ score }}/{{ questions.length }}</h2>
        <v-card-actions class="justify-center">
          <v-btn class="mx-10" @click="restartQuiz">Ricomincia <v-icon right>mdi-reload</v-icon></v-btn>
          <v-btn class="mx-10" @click="$router.push('/')">Seleziona <v-icon right>mdi-home</v-icon></v-btn>
        </v-card-actions>
          <v-divider class="mt-5"></v-divider>
      </v-card>

      <v-card-actions class="justify-center">
        <v-btn class="mx-10" color="blue-grey darken-1" @click="skipQuestion">
          Salta
        </v-btn>
        <v-btn :disabled="!next" class="mx-10" color="blue-grey darken-1" @click="nextQuestion">
          Avanti
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data(){
    return{
      questions: [
        {
          detail:"",
          question:'Come si chiama la società segreta di Eva, controparte di Sic Mundus Creatus Est?',
          suggestions: [
            {suggestion:'Ferit Lux'},
            {suggestion:'Erit Lucis'},
            {suggestion:'Erit Lux', correct: true},
            {suggestion:'Ferit Lucem'},
          ]
        },
        {
          detail:"",
          question:"In che giorno avviene l'apocalisse nel mondo di Eva?",
          suggestions: [
            {suggestion:'8 novembre 2020'},
            {suggestion:'8 novembre 2019', correct: true},
            {suggestion:'7 novembre 2019'},
            {suggestion:'7 novembre 2020'},
          ]
        },
        {
          detail:"",
          question:"Quante persone vengono uccise da The Unknown nel corso della terza stagione?",
          suggestions: [
            {suggestion:'4'},
            {suggestion:'3', correct: true},
            {suggestion:'1'},
            {suggestion:'2'},
          ]
        },
        {
          detail:"",
          question:"Come si chiama il padre biologico di Helge Doppler?",
          suggestions: [
            {suggestion:'Bernd Doppler'},
            {suggestion:'Sebastian Kruger'},
            {suggestion:'Anatol Veliev', correct: true},
            {suggestion:'Hermann Albers'},
          ]
        },
        {
          detail:"",
          question:"Chi provoca l'incidente della centrale nucleare, il 21 giugno del 1986?",
          suggestions: [
            {suggestion:'Bernd Doppler'},
            {suggestion:'The Unknown', correct: true},
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'Noah'},
          ]
        },
        {
          detail:"",
          question:"In quale anno finiscono Lo Straniero e i ragazzi dopo essersi salvati dall'apocalisse?",
          suggestions: [
            {suggestion:'1988'},
            {suggestion:'1887'},
            {suggestion:'1888', correct: true},
            {suggestion:'1889'},
          ]
        },
        {
          detail:"",
          question:"Come si chiama il Tannhaus cieco che, nel 1888, accoglie Lo Straniero e i ragazzi?",
          suggestions: [
            {suggestion:'Heinrich'},
            {suggestion:'Gustav', correct: true},
            {suggestion:'Leopold'},
            {suggestion:'Anatol'},
          ]
        },
        {
          detail:"",
          question:"Da quanti ragazzi è composto il gruppo che va a visitare le grotte, la sera del 4 novembre 2019, nel mondo di Eva?",
          suggestions: [
            {suggestion:'4'},
            {suggestion:'5', correct: true},
            {suggestion:'6'},
            {suggestion:'Nessuna delle precedenti'},
          ]
        },
        {
          detail:"",
          question:"Cosa non ha Torben Wöller nel mondo di Eva?",
          suggestions: [
            {suggestion:"L'occhio destro"},
            {suggestion:'Il braccio destro'},
            {suggestion:"L'orecchio destro"},
            {suggestion:'Il braccio sinistro', correct: true},
          ]
        },
        {
          detail:"",
          question:"Chi è la nonna materna di Tronte?",
          suggestions: [
            {suggestion:'Charlotte'},
            {suggestion:'Silja', correct: true},
            {suggestion:'Martha 2'},
            {suggestion:'Hannah'},
          ]
        },
        {
          detail:"",
          question:"Da chi viene salvato Jonas mentre tenta di impiccarsi nella soffitta di casa sua?",
          suggestions: [
            {suggestion:'Da Noah', correct: true},
            {suggestion:'Da Elisabeth'},
            {suggestion:'Da Claudia'},
            {suggestion:'Da Adam'},
          ]
        },
        {
          detail:"",
          question:"Come si chiama la madre di Peter Doppler e moglie di Helge Doppler?" ,
          suggestions: [
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'Greta Doppler'},
            {suggestion:'Ulla Schmidt', correct: true},
            {suggestion:'Jasmin Trewen'},
          ]
        },
        {
          detail:"",
          question:"Chi rivela a Bartosz la vera identità di Adam?",
          suggestions: [
            {suggestion:'Lo straniero'},
            {suggestion:'Magnus'},
            {suggestion:'Gustav Tannhaus'},
            {suggestion:'Martha 2', correct: true},
          ]
        },
        {
          detail:"",
          question:"Per mano di chi muore Ulrich nel mondo 2, nel 1987?",
          suggestions: [
            {suggestion:"Helge"},
            {suggestion:"Egon"},
            {suggestion:'Helge anziano', correct: true},
            {suggestion:'The Unknown'},
          ]
        },
        {
          detail:"",
          question:"Chi ha scritto il diario con il simbolo della triquetra, nel quale vengono narrati gli eventi verificatisi a Winden?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Claudia (mondo di Eva)"},
            {suggestion:'Eva'},
            {suggestion:'The Unknown', correct: true},
          ]
        },
        {
          detail:"",
          question:"Dove si trova la sede di Erit Lux?",
          suggestions: [
            {suggestion:"Sotto la chiesa di Winden"},
            {suggestion:"In mezzo al bosco"},
            {suggestion:'Dentro le grotte di Winden', correct: true},
            {suggestion:'Nessuna delle precedenti'},
          ]
        },
        {
          detail:"",
          question:"Cosa prende The Unknown dalla sede del Sic Mundus, prima di darle fuoco?",
          suggestions: [
            {suggestion:"I progetti sul dispositivo di Tannhaus", correct: true},
            {suggestion:"I progetti della sfera dorata"},
            {suggestion:'Un fascicolo sulla materia oscura'},
            {suggestion:'Il diario del Sic Mundus'},
          ]
        },
        {
          detail:"",
          question:"Come si chiama il figlio di H.G. Tannhaus?",
          suggestions: [
            {suggestion:"Malek"},
            {suggestion:"Alek"},
            {suggestion:'Maren'},
            {suggestion:"Marek", correct: true},
          ]
        },
        {
          detail:"",
          question:"Chi è il fondatore della società segreta Sic Mundus Creatus Est?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Heinrich Tannhaus", correct: true},
            {suggestion:'Hanno Tauber'},
            {suggestion:"Gustav Tannhaus"},
          ]
        },
         {
          detail:"",
          question:"Di che colore sono le tute antiradiazione nel mondo 2?",
          suggestions: [
            {suggestion:"Verdi"},
            {suggestion:"Rosse", correct: true},
            {suggestion:'Gialle'},
            {suggestion:"Blu"},
          ]
        },
         {
          detail:"",
          question:"Che legame di parentela c'è tra Charlotte Doppler e Tronte Nielsen?",
          suggestions: [
            {suggestion:'Zia e nipote'},
            {suggestion:'Cugini', correct: true},
            {suggestion:'Nonno e nipote'},
            {suggestion:'Non hanno alcun legame di parentela'},
          ]
        },
         {
          detail:"",
          question:"Che legame di parentela c'è tra Helge Doppler e Jonas Kahnwald?",
          suggestions: [
            {suggestion:"Bisnonno e pronipote"},
            {suggestion:"Prozio e pronipote"},
            {suggestion:'Cugini'},
            {suggestion:'Non hanno alcun legame di parentela', correct: true},
          ]
        },
        {
          detail:"",
          question:"Chi è il nonno materno di Bartosz Tiedemann?",
          suggestions: [
            {suggestion:"Tronte Nielsen"},
            {suggestion:"Egon Tiedemann"},
            {suggestion:'Bernd Doppler', correct: true},
            {suggestion:"Helge Doppler"},
          ]
        },
        {
          detail:"",
          question:"Oltre ad essere suo bisnonno, che legame di parentela ha Egon con Bartosz?",
          suggestions: [
            {suggestion:"Bartosz è il suocero di Egon"},
            {suggestion:"Egon è lo zio di Bartosz"},
            {suggestion:'Egon è il suocero di Bartosz', correct: true},
            {suggestion:"Bartosz è il cugino di Egon"},
          ]
        },
        {
          detail:"",
          question:"Quale dei seguenti personaggi non potrebbe esistere nel mondo di origine?",
          suggestions: [
            {suggestion:'Aleksander Tiedemann'},
            {suggestion:'Katharina Albers'},
            {suggestion:'Franziska Doppler', correct: true},
            {suggestion:'Claudia Tiedemann'},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
      lockSelection: false,
      dialog: false,
    }
  },
  
  methods:{
    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
}
</script>