<template>
  <div class="dark2Eng">
    <span class="bg2"></span>
    <v-card id="container" dark max-width="600" class="mx-auto">
      <v-toolbar extended class="mt-1" color="blue-grey" dark>
        <v-toolbar-title class="flex text-center">
          <h2 class="text-center mt-10">Quiz Second Season</h2>
        </v-toolbar-title>
      </v-toolbar>

      <v-progress-linear color="lime lighten-1" :value="progress"></v-progress-linear>

      <v-list flat subheader two-line v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">

      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-layout align-end justify-end>
          <v-btn v-bind="attrs" v-on="on" icon text color="lime lighten-1" :disabled="!next">
            <v-icon>mdi-information</v-icon>
          </v-btn>
          </v-layout>
        </template>
        
        <v-card dark>
          <v-card-title class="headline">
            {{ element.question }}
          </v-card-title>

          <v-card-text class="my-5">
            {{ element.detail }}
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-layout align-center justify-center>
              <v-btn color="primary" text @click="dialog = false">
              Close
              </v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <h1 class="text-center">Question {{ b }}/{{ questions.length }}</h1>
      
      <v-list-item class="d-flex justify-center text-center" >{{ element.question }}</v-list-item>
      <v-divider class="mt-10"></v-divider>

      <v-list-item-group color="blue-grey--text">
        <template v-for="(item, index) in element.suggestions">
      <v-list-item id="list" class="d-flex justify-center my-2" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)" :disabled="lockSelection">
        {{ item.suggestion }}
        <v-icon class="mx-5" color="white" v-if="select ? item.correct : ''">mdi-check</v-icon>
        <v-icon class="mx-5" color="white" v-if="select ? !item.correct : ''">mdi-close</v-icon>
      </v-list-item>
      <v-divider v-if="index < element.suggestions.length - 1" :key="`${index}-divider`"></v-divider>
      </template>
      </v-list-item-group>
    </v-list>

      <v-divider></v-divider>

      <v-card dark max-width="600" class="mx-auto" v-if="score_show">
        <h2 class="text-center my-4">Your Score </h2>
        <h2 class="text-center my-4">{{ score }}/{{ questions.length }}</h2>
        <v-card-actions class="justify-center">
          <v-btn class="mx-10" @click="restartQuiz">Restart <v-icon right>mdi-reload</v-icon></v-btn>
          <v-btn class="mx-10" @click="$router.push('/en')">Select <v-icon right>mdi-home</v-icon></v-btn>
        </v-card-actions>
          <v-divider class="mt-5"></v-divider>
      </v-card>

      <v-card-actions class="justify-center">
        <v-btn class="mx-10" color="blue-grey darken-1" @click="skipQuestion">
          Skip
        </v-btn>
        <v-btn :disabled="!next" class="mx-10" color="blue-grey darken-1" @click="nextQuestion">
          Next
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data(){
    return{
      questions: [
        {
          detail:"At the start of the season, two people work on the tunnel in 1921: Noah as a boy and the one who is probably an adult Bartosz. After a talk between the two, the former will kill the latter with a pickaxe.",
          question:'How many people are working in the caves to dig the tunnels on June 21, 1921?',
          suggestions: [
            {suggestion:'1'},
            {suggestion:'3'},
            {suggestion:'2', correct: true},
            {suggestion:'No one'},
          ]
        },
        {
          detail:"To support Charlotte in the investigation of missing persons, there will be Commissioner Clausen, an enigmatic person and in search of answers on the disappearance of his brother, which took place 34 years earlier.",
          question:"What is the name of the detective who will join the Winden police in investigating the people missing, since the beginning of the second season?",
          suggestions: [
            {suggestion:'Torben Wöller'},
            {suggestion:'Clausen', correct: true},
            {suggestion:'Jankowski'},
            {suggestion:'Nielsen'},
          ]
        },
        {
          detail:"In 2053, we see a group of apocalypse survivors led by adult Elisabeth, who urges everyone to stay away from the 'dead zone' by hanging those who break the rules.",
          question:"Who is the leader of the survivors in the Winden of 2053?",
          suggestions: [
            {suggestion:'Claudia'},
            {suggestion:'Elisabeth', correct: true},
            {suggestion:'Silja'},
            {suggestion:'Agnes'},
          ]
        },
        {
          detail:"Claudia Tiedemann receives a strange visit to the nuclear power plant from a very old woman. This is none other than her own older, explaining to her the existence of time travel.",
          question:"Who reveals to adult Claudia about the existence of time travel, in 1987?",
          suggestions: [
            {suggestion:'Noah'},
            {suggestion:'Claudia elderly', correct: true},
            {suggestion:'Jonas'},
            {suggestion:'Helge'},
          ]
        },
        {
          detail:"The Stranger brings Hannah in 1987 to prove the existence of time travel. Here Hannah will see Mikkel as a child and understand the incredible paradox of who will become her husband.",
          question:"In what era does The Stranger bring Hannah into the future thanks to Tannhaus's device?",
          suggestions: [
            {suggestion:'In 1954'},
            {suggestion:'In 1986'},
            {suggestion:'In 1987', correct: true},
            {suggestion:'In 1921'},
          ]
        },
        {
          detail:"After the elderly Claudia explains everything she knows to herself as an adult, she returns in 1954 where she will bury the briefcase so that her younger version can find it in 1987.",
          question:"How does Claudia get Tannhaus's time travel device?",
          suggestions: [
            {suggestion:'The Stranger hands it to she'},
            {suggestion:'Noah gives it to she'},
            {suggestion:'She finds him buried in the backyard', correct: true},
            {suggestion:'The elderly Claudia gives it to she'},
          ]
        },
        {
          detail:"Helge as a child returns in 1954 (after 7 months) thanks to the chair in the bunker, now functional. From this moment the relationship between Noah and Helge will begin that will lead them to collaborate in the kidnappings.",
          question:"How does Helge as a child come back in 1954?",
          suggestions: [
            {suggestion:'With the chair in the bunker', correct: true},
            {suggestion:'Traveling through the caves'},
            {suggestion:'With the Tannhaus device'},
            {suggestion:'With dark matter'},
          ]
        },
        {
          detail:"Agnes reveals to Noah that the last pages of the diary are in the hands of the elderly Claudia; but, in exchange, Noah will have to convince Adam to readmit Agnes to Sic Mundus before the last cycle begins.",
          question:"Who tells Noah where the missing pages of the Sic Mundis book are?",
          suggestions: [
            {suggestion:'Young Noah'},
            {suggestion:'Claudia'},
            {suggestion:'Agnes', correct: true},
            {suggestion:'Adam'},
          ]
        },
        {
          detail:"Adult Claudia goes in 2020, here she will find out about the various events that will happen in Winden in the future. He will find out when his father died and he will personally see his daughter Regina with cancer.",
          question:"In which era does Claudia Tiedemann arrive in her first time travel, thanks to the Tannhaus device?",
          suggestions: [
            {suggestion:'2019'},
            {suggestion:'2020', correct: true},
            {suggestion:'1954'},
            {suggestion:'1986'},
          ]
        },
        {
          detail:"In 2053, Jonas, entering the stabilized dark matter, travels to 1921. Arrived he will meet Erna who will host him, in addition, he will meet a boy who will prove that he knows who he is.",
          question:"What era does Jonas arrive at after traveling through dark matter in 2053?",
          suggestions: [
            {suggestion:'1920'},
            {suggestion:'1921', correct: true},
            {suggestion:'1986'},
            {suggestion:'2020'},
          ]
        },
        {
          detail:"In 2053, while Jonas tries to stabilize the dark matter, Silja arrives, who later, after Jonas passes through the stabilized dark matter, meets Elisabeth and asks about it.",
          question:"Who is present with Jonas in the nuclear power plant when he enters dark matter, in 2053?",
          suggestions: [
            {suggestion:'Silja', correct: true},
            {suggestion:'Elisabeth adult'},
            {suggestion:'Both'},
            {suggestion:'No one'},
          ]
        },
        {
          detail:"In the bunker, Charlotte, Peter, The Stranger and Hannah discuss what happened. Charlotte offers to reveal to Katharina where Mikkel and Ulrich are, but Katharina won't believe a word.",
          question:"Who reveals to Katharina that Mikkel and Ulrich are alive but in different eras?",
          suggestions: [
            {suggestion:'Charlotte', correct: true},
            {suggestion:'Peter'},
            {suggestion:'Hannah'},
            {suggestion:'The Stranger'},
          ]
        },
        {
          detail:"The mystery boy accompanies Jonas to the church, where he meets Noah who reveals that the boy is himself young. He then accompanies him to Adam, who will reveal to him that he is himself but an old man.",
          question:"Who meets Jonas after being taken to Sic Mundus headquarters?" ,
          suggestions: [
            {suggestion:'Adam', correct: true},
            {suggestion:'Magnus'},
            {suggestion:'Agnes'},
            {suggestion:'Franziska'},
          ]
        },
        {
          detail:"Ulrich, during a visit from Egon, discovers that his son Mikkel is in the same year as him; He then implements a plan to escape and search for him. But he is stopped by the police who take him back to the institution.",
          question:"Why did Ulrich Nielsen run away from the psychiatric hospital where he was imprisoned in 1987?",
          suggestions: [
            {suggestion:'To kill Egon'},
            {suggestion:'To meet the mother'},
            {suggestion:'To look for Mikkel', correct: true},
            {suggestion:'None of these'},
          ]
        },
         {
          detail:"After convincing Bartosz to show them how to use the time machine, Martha and the others travel to 1987 where they see Ulrich being taken back to the institution in the police car.",
          question:"After traveling in time for the first time, in what era do Martha and the other boys find themselves?",
          suggestions: [
            {suggestion:'1986'},
            {suggestion:'2019'},
            {suggestion:'1987', correct: true},
            {suggestion:'1954'},
          ]
        },
         {
          detail:"As Charlotte searches for clues in Grandpa's shop, she receives a visit from Noah, who seems to know her very well. The man reveals several things to her, including the fact that he is her father.",
          question:"What does Charlotte discover when she talks to Noah, inside the old Tannhaus shop?",
          suggestions: [
            {suggestion:"The identity of the mother"},
            {suggestion:"The identity of the father", correct: true},
            {suggestion:'Both things'},
            {suggestion:'None of these'},
          ]
        },
         {
          detail:"After the conversation with Adam, Adam convinces Jonas that he can change events by going back to where it all began. Jonas, therefore, will go on June 20, 2019, the day before his father's suicide.",
          question:"What day and year does Jonas arrive after traveling with dark matter at Sic Mundus headquarters?",
          suggestions: [
            {suggestion:"20 June 2020"},
            {suggestion:"21 June 2019"},
            {suggestion:'20 June 2019', correct: true},
            {suggestion:'None of these'},
          ]
        },
        {
          detail:"The boys find the medallion of St Christopher, the protector of travelers. The same medallion that The Stranger will leave in Martha's room, making her understand that he is back in his time.",
          question:"What do Martha and Jonas find in the sand when they are at the lake?",
          suggestions: [
            {suggestion:"The medallion of St Ann"},
            {suggestion:"The medallion of St Christopher", correct: true},
            {suggestion:'The medallion of St Lorenz'},
            {suggestion:'The medallion of St Claud'},
          ]
        },
        {
          detail:"As Jonas tries to stop Michael from committing suicide, Claudia arrives who seems to know both of them. She convinces Jonas to leave his father to his fate, so that everything goes as it always has.",
          question:"Who enters the Kahnwald house while Jonas tries to stop his father from hanging himself?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Noah"},
            {suggestion:'Claudia', correct: true},
            {suggestion:'Hannah'},
          ]
        },
        {
          detail:"Hannah uses the briefcase to go to 1954. Here, after seeing UIrich and leaving him to his fate, she introduces herself to Egon as 'Katharina Nielsen'; it is assumed that the two will have a relationship.",
          question:"What era does Hannah arrive in on her second time travel?",
          suggestions: [
            {suggestion:"1954", correct: true},
            {suggestion:"1953"},
            {suggestion:'1986'},
            {suggestion:'1987'},
          ]
        },
        {
          detail:"Clausen discovers that Aleksander stole the identity of his brother who disappeared 34 years ago (Aleksander Köhler), so he arrests him; in fact Aleksander will be in prison at the moment of the apocalypse.",
          question:"Why does detective Clausen arrest Aleksander Tiedemann?",
          suggestions: [
            {suggestion:"For the murder of his brother"},
            {suggestion:"For a robbery"},
            {suggestion:'For bank fraud'},
            {suggestion:"For identity theft", correct: true},
          ]
        },
        {
          detail:"After Claudia unintentionally kills her father, she returns home where Jonas will arrive saying that her elderly self has told him what she just did. So he will convince her to follow him into the future.",
          question:"Who enters Claudia Tiedemann's house in 1987 to bring her in 2020?",
          suggestions: [
            {suggestion:"Claudia elderly"},
            {suggestion:"Jonas", correct: true},
            {suggestion:'Helge'},
            {suggestion:"Agnes"},
          ]
        },
         {
           detail:"Just before the apocalypse breaks out, Peter and Elisabeth arrive in the bunker, then the adult Claudia and Regina arrive. Lastly, the boy Noah arrives (from 1921) who will meet Elisabeth for the first time.",
          question:"How many people are present in the bunker moments before the apocalypse breaks out?",
          suggestions: [
            {suggestion:"4"},
            {suggestion:"5", correct: true},
            {suggestion:'3'},
            {suggestion:"6"},
          ]
        },
        {
          detail:"Before going into the bunker, Boy Noah gives The Stranger a letter from Martha. content will upset the man, who will rush to save the boys, taking them away with the briefcase.",
          question:"Which characters were saved by the Stranger shortly before the apocalypse?",
          suggestions: [
            {suggestion:"Magnus, Martha and Franziska"},
            {suggestion:"Elisabeth, Franziska and Bartosz"},
            {suggestion:'Martha, Magnus and Elisabeth'},
            {suggestion:"Bartosz, Magnus and Franziska", correct: true},
          ]
        },
        {
          detail:"Adam kills Martha, so that this pain leads Jonas to become him. Jonas himself will then be saved from the apocalypse by a Martha from another world, who will take him with her.",
          question:"Who dies moments before the apocalypse hits Winden?",
          suggestions: [
            {suggestion:"Elisabeth adult"},
            {suggestion:"Hannah"},
            {suggestion:'Martha', correct: true},
            {suggestion:"Charlotte"},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
      lockSelection: false,
      dialog: false,
    }
  },
  
  methods:{
    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
}
</script>