<template>
  <div class="dark3Eng">
    <span class="bg3"></span>
    <v-card id="container" dark max-width="600" class="mx-auto">
      <v-toolbar extended class="mt-1" color="blue-grey" dark>
        <v-toolbar-title class="flex text-center">
          <h2 class="text-center mt-10">Quiz Third Season </h2>
        </v-toolbar-title>
      </v-toolbar>

      <v-progress-linear color="lime lighten-1" :value="progress"></v-progress-linear>

      <v-list flat subheader two-line v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">

      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-layout align-end justify-end>
          <v-btn v-bind="attrs" v-on="on" icon text disabled>
            <v-icon>mdi-information</v-icon>
          </v-btn>
          </v-layout>
        </template>
        
        <v-card dark>
          <v-card-title class="headline">
            {{ element.question }}
          </v-card-title>

          <v-card-text class="my-5">
            {{ element.detail }}
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-layout align-center justify-center>
              <v-btn color="primary" text @click="dialog = false">
              Close
              </v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <h1 class="text-center">Question {{ b }}/{{ questions.length }}</h1>
      
      <v-list-item class="d-flex justify-center text-center" >{{ element.question }}</v-list-item>
      <v-divider class="mt-10"></v-divider>

      <v-list-item-group color="blue-grey--text">
        <template v-for="(item, index) in element.suggestions">
      <v-list-item id="list" class="d-flex justify-center my-2" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)" :disabled="lockSelection">
        {{ item.suggestion }}
        <v-icon class="mx-5" color="white" v-if="select ? item.correct : ''">mdi-check</v-icon>
        <v-icon class="mx-5" color="white" v-if="select ? !item.correct : ''">mdi-close</v-icon>
      </v-list-item>
      <v-divider v-if="index < element.suggestions.length - 1" :key="`${index}-divider`"></v-divider>
      </template>
      </v-list-item-group>
    </v-list>

      <v-divider></v-divider>

      <v-card dark max-width="600" class="mx-auto" v-if="score_show">
        <h2 class="text-center my-4">Your Score </h2>
        <h2 class="text-center my-4">{{ score }}/{{ questions.length }}</h2>
        <v-card-actions class="justify-center">
          <v-btn class="mx-10" @click="restartQuiz">Restart <v-icon right>mdi-reload</v-icon></v-btn>
          <v-btn class="mx-10" @click="$router.push('/en')">Select <v-icon right>mdi-home</v-icon></v-btn>
        </v-card-actions>
          <v-divider class="mt-5"></v-divider>
      </v-card>

      <v-card-actions class="justify-center">
        <v-btn class="mx-10" color="blue-grey darken-1" @click="skipQuestion">
          Skip
        </v-btn>
        <v-btn :disabled="!next" class="mx-10" color="blue-grey darken-1" @click="nextQuestion">
          Next
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data(){
    return{
      questions: [
        {
          detail:"",
          question:'What is the name of the secret society of Eve, counterpart of Sic Mundus Creatus Est?',
          suggestions: [
            {suggestion:'Ferit Lux'},
            {suggestion:'Erit Lucis'},
            {suggestion:'Erit Lux', correct: true},
            {suggestion:'Ferit Lucem'},
          ]
        },
        {
          detail:"",
          question:"On what day does the apocalypse take place in the world of Eve?",
          suggestions: [
            {suggestion:'8 November 2020'},
            {suggestion:'8 November 2019', correct: true},
            {suggestion:'7 November 2019'},
            {suggestion:'7 November 2020'},
          ]
        },
        {
          detail:"",
          question:"How many people are killed by The Unknown during the third season?",
          suggestions: [
            {suggestion:'4'},
            {suggestion:'3', correct: true},
            {suggestion:'1'},
            {suggestion:'2'},
          ]
        },
        {
          detail:"",
          question:"What is the name of Helge Doppler's biological father?",
          suggestions: [
            {suggestion:'Bernd Doppler'},
            {suggestion:'Sebastian Kruger'},
            {suggestion:'Anatol Veliev', correct: true},
            {suggestion:'Hermann Albers'},
          ]
        },
        {
          detail:"",
          question:"Who Causes the Nuclear Power Plant Accident on June 21, 1986?",
          suggestions: [
            {suggestion:'Bernd Doppler'},
            {suggestion:'The Unknown', correct: true},
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'Noah'},
          ]
        },
        {
          detail:"",
          question:"In which year do The Stranger and the boys finish after being saved from the apocalypse?",
          suggestions: [
            {suggestion:'1988'},
            {suggestion:'1887'},
            {suggestion:'1888', correct: true},
            {suggestion:'1889'},
          ]
        },
        {
          detail:"",
          question:"What is the name of the blind Tannhaus which, in 1888, welcomed The Stranger and the boys?",
          suggestions: [
            {suggestion:'Heinrich'},
            {suggestion:'Gustav', correct: true},
            {suggestion:'Leopold'},
            {suggestion:'Anatol'},
          ]
        },
        {
          detail:"",
          question:"How many young people is in the group that goes to visit the caves, on the evening of November 4, 2019, in the world of Eve?",
          suggestions: [
            {suggestion:'4'},
            {suggestion:'5', correct: true},
            {suggestion:'6'},
            {suggestion:'None of the above'},
          ]
        },
        {
          detail:"",
          question:"What does Torben Wöller not have in Eva's world?",
          suggestions: [
            {suggestion:"The right eye"},
            {suggestion:'The right arm'},
            {suggestion:"The right ear"},
            {suggestion:'The left arm', correct: true},
          ]
        },
        {
          detail:"",
          question:"Who is Tronte's maternal grandmother?",
          suggestions: [
            {suggestion:'Charlotte'},
            {suggestion:'Silja', correct: true},
            {suggestion:'Martha 2'},
            {suggestion:'Hannah'},
          ]
        },
        {
          detail:"",
          question:"From whom is Jonas saved while trying to hang himself in the attic of his house?",
          suggestions: [
            {suggestion:'From Noah', correct: true},
            {suggestion:'From Elisabeth'},
            {suggestion:'From Claudia'},
            {suggestion:'From Adam'},
          ]
        },
        {
          detail:"",
          question:"What is the name of Peter Doppler's mother and Helge Doppler's wife?" ,
          suggestions: [
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'Greta Doppler'},
            {suggestion:'Ulla Schmidt', correct: true},
            {suggestion:'Jasmin Trewen'},
          ]
        },
        {
          detail:"",
          question:"Who reveals Adam's true identity to Bartosz?",
          suggestions: [
            {suggestion:'The Stranger'},
            {suggestion:'Magnus'},
            {suggestion:'Gustav Tannhaus'},
            {suggestion:'Martha 2', correct: true},
          ]
        },
        {
          detail:"",
          question:"By whose hand did Ulrich die in world 2, in 1987?",
          suggestions: [
            {suggestion:"Helge"},
            {suggestion:"Egon"},
            {suggestion:'Helge elder', correct: true},
            {suggestion:'The Unknown'},
          ]
        },
        {
          detail:"",
          question:"Who wrote the diary with the triquetra symbol, in which the events that took place in Winden are narrated?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Claudia (Eva's world)"},
            {suggestion:'Eva'},
            {suggestion:'The Unknown', correct: true},
          ]
        },
        {
          detail:"",
          question:"Where is the Erit Lux headquarters located?",
          suggestions: [
            {suggestion:"Under the Winden church"},
            {suggestion:"In the middle of the woods"},
            {suggestion:'Inside the Winden caves', correct: true},
            {suggestion:'None of the above'},
          ]
        },
        {
          detail:"",
          question:"What does The Unknown take from the Sic Mundus headquarters, before setting it on fire?",
          suggestions: [
            {suggestion:"The projects on the Tannhaus device", correct: true},
            {suggestion:"The projects of the golden sphere"},
            {suggestion:'A dossier on dark matter'},
            {suggestion:'The Sic Mundus diary'},
          ]
        },
        {
          detail:"",
          question:"What is the name of the son of H.G. Tannhaus?",
          suggestions: [
            {suggestion:"Malek"},
            {suggestion:"Alek"},
            {suggestion:'Maren'},
            {suggestion:"Marek", correct: true},
          ]
        },
        {
          detail:"",
          question:"Who is the founder of the secret society Sic Mundus Creatus Est?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Heinrich Tannhaus", correct: true},
            {suggestion:'Hanno Tauber'},
            {suggestion:"Gustav Tannhaus"},
          ]
        },
         {
          detail:"",
          question:"What color are the radiation suits in world 2?",
          suggestions: [
            {suggestion:"Green"},
            {suggestion:"Red", correct: true},
            {suggestion:'Yellow'},
            {suggestion:"Blue"},
          ]
        },
         {
          detail:"",
          question:"What is the family relationship between Charlotte Doppler and Tronte Nielsen?",
          suggestions: [
            {suggestion:'Aunt and niece'},
            {suggestion:'Cousins', correct: true},
            {suggestion:'Grandfather and grandson'},
            {suggestion:'They have no family relationship'},
          ]
        },
         {
          detail:"",
          question:"What is the relationship between Helge Doppler and Jonas Kahnwald?",
          suggestions: [
            {suggestion:"Great-grandfather and great-grandson"},
            {suggestion:"Great-uncle and great-grandson"},
            {suggestion:'Cousins'},
            {suggestion:'They have no family relationship', correct: true},
          ]
        },
        {
          detail:"",
          question:"Who is Bartosz Tiedemann's maternal grandfather?",
          suggestions: [
            {suggestion:"Tronte Nielsen"},
            {suggestion:"Egon Tiedemann"},
            {suggestion:'Bernd Doppler', correct: true},
            {suggestion:"Helge Doppler"},
          ]
        },
        {
          detail:"",
          question:"Besides being his great-grandfather, how is Egon related to Bartosz?",
          suggestions: [
            {suggestion:"Bartosz is Egon's father-in-law"},
            {suggestion:"Egon is Bartosz's uncle"},
            {suggestion:"Egon is Bartosz's father-in-law", correct: true},
            {suggestion:"Bartosz is Egon's cousin"},
          ]
        },
        {
          detail:"",
          question:"Which of the following characters might not exist in the world of origin?",
          suggestions: [
            {suggestion:'Aleksander Tiedemann'},
            {suggestion:'Katharina Albers'},
            {suggestion:'Franziska Doppler', correct: true},
            {suggestion:'Claudia Tiedemann'},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
      lockSelection: false,
      dialog: false,
    }
  },
  
  methods:{
    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
}
</script>