import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Dark1 from '../views/Dark1.vue'
import Dark2 from '../views/Dark2.vue'
import Dark3 from '../views/Dark3.vue'
import homeEng from '../views/homeEng.vue'
import dark1Eng from '../views/dark1Eng.vue'
import dark2Eng from '../views/dark2Eng.vue'
import dark3Eng from '../views/dark3Eng.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/dark1',
    name: 'dark1',
    component: Dark1
  },
  {
    path: '/dark2',
    name: 'dark2',
    component: Dark2
  },
  {
    path: '/dark3',
    name: 'dark3',
    component: Dark3
  },
  {
    path: '/en',
    name: 'homeEng',
    component: homeEng
  },
  {
    path: '/dark1en',
    name: 'dark1Eng',
    component: dark1Eng
  },
  {
    path: '/dark2en',
    name: 'dark2Eng',
    component: dark2Eng
  },
  {
    path: '/dark3en',
    name: 'dark3Eng',
    component: dark3Eng
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
